import React from "react";
import moment from "moment";

const EventModal = ({ selectedEvent, setShowModal }) => {
  return (
    <div className="fixed top-0 left-0 w-full h-full bg-black bg-opacity-50 flex justify-center items-center">
      <div className="bg-white p-4 rounded shadow-md w-80">
        <h2 className="text-xl font-bold mb-2">{selectedEvent.title}</h2>
        <p className="text-sm">AppointmentId: {selectedEvent.appointmentId}</p>
        <p className="text-sm">
          Start: {moment(selectedEvent.start).format("MMMM Do YYYY, h:mm a")}
        </p>
        <p className="text-sm">
          End: {moment(selectedEvent.end).format("MMMM Do YYYY, h:mm a")}
        </p>
        <p className="text-sm">PatientId: {selectedEvent.patientId}</p>
        <p className="text-sm">PatientName: {selectedEvent.patient}</p>
        <p className="text-sm">DoctorId: {selectedEvent.doctorId}</p>
        <p className="text-sm">DoctorName: {selectedEvent.doctor}</p>

        <button
          className="mt-4 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
          onClick={() => setShowModal(false)}
        >
          Close
        </button>
      </div>
    </div>
  );
};

export default EventModal;
